body{
    background: #333945;
    display: flex;
    justify-content: center;
}

h1{
    color: #fff;
}
.button{
    background-color: cyan;
    border: none;
    color: #fff;
    padding: 15px 30px;
    text-align: center;
    text-decoration: none;
    font-size: 20px;
    border-radius: 10px;
    text-shadow: 0 2px 3px #000;
    margin-top: 5px;
    margin-right: 5px;
    min-width: 200px;
}